import styled from "styled-components";

export const InputStyled = styled.input `
    display: block;
    padding: 10px 0px 10px 15px;
    font-size:1.4rem;
    font-weight:400;
    border-radius:3px;
    margin-top:6px;
    width: 100%;
    background:var(--inputBackground);
    border:var(--inputBorder) 1px solid;

`

export const LabelStyled = styled.label `
      display:block;
      color:var(--veryGrey);
      font-size:1.4rem;
      margin-bottom:35px;
      font-weight:700;
`